import React, { useEffect, useState } from "react";
import { Card, CardBody, Heading, Text } from "@pancakeswap-libs/uikit";
import BigNumber from "bignumber.js/bignumber";
import styled from "styled-components";
import { getBalanceNumber } from "utils/formatBalance";
import { useTotalSupply, useBurnedBalance } from "hooks/useTokenBalance";
import { getCakeAddress } from "utils/addressHelpers";
import upwardTokenABI from "config/abi/upwardToken.json";
import { getContract } from "utils/web3";
import CardValue from "./CardValue";
import { useFarms, usePriceCakeBusd } from "../../../state/hooks";

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
  border-radius: 0;
  border: 1px solid #ff9372;
  background-color: #100000;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

const CakeStats = () => {
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getCakeAddress())
  const farms = useFarms();
  const upwardPrice = usePriceCakeBusd();
  const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0);
  const cakeSupply = getBalanceNumber(circSupply);
  const marketCap = upwardPrice.times(circSupply);
  const [maxTransferAmount, setMaxTransferAmount] = useState(0);
  const [transferTax, setTransferTax] = useState(0);

  let upwardPerBlock = 0;
  if (farms && farms[0] && farms[0].upwardPerBlock) {
    upwardPerBlock = new BigNumber(farms[0].upwardPerBlock).div(new BigNumber(10).pow(18)).toNumber();
  }

  useEffect(() => {
    async function GetMaxTransferAmount() {
      const cakeContract = getContract(upwardTokenABI, getCakeAddress());
      const response = await cakeContract.methods.maxTransferAmount().call();
      const mta = new BigNumber(response).div(new BigNumber("1000000000000000000")).toFixed(5);
      const mta2 = new BigNumber(mta).toNumber();
      setMaxTransferAmount(mta2);

      const response2 = await cakeContract.methods.transferTaxRate().call();
      const ttr = new BigNumber(response2).dividedBy(100).toNumber();
      setTransferTax(ttr);
    }

    GetMaxTransferAmount();
  });

  return (
    <StyledCakeStats>
      <CardBody>
        <Heading size="xl" mb="24px">
          UPWARD Stats
        </Heading>
        <Row>
          <Text fontSize="14px">Market Cap</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(marketCap)} decimals={0} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="14px">Circulating Supply</Text>
          {cakeSupply && <CardValue fontSize="14px" value={cakeSupply} decimals={0} />}
        </Row>
        <Row>
          <Text fontSize="14px">Total Burned</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(burnedBalance)} decimals={0} />
        </Row>
        <Row>
          <Text fontSize="14px">Total Minted</Text>
          {totalSupply && <CardValue fontSize="14px" value={getBalanceNumber(totalSupply)} decimals={0} />}
        </Row>
        <Row>
          <Text fontSize="14px">New UPWARD/block</Text>
          <Text bold fontSize="14px">{upwardPerBlock}</Text>
        </Row>
        <Row>
          <Text fontSize="14px">Max Transaction Amount</Text>
          <CardValue fontSize="14px" decimals={0} value={maxTransferAmount} />
        </Row>
        <Row>
          <Text fontSize="14px">Transfer Tax</Text>
          <CardValue fontSize="14px" decimals={1} value={transferTax} suffix="%" />
        </Row>
      </CardBody>
    </StyledCakeStats>
  )
}

export default CakeStats
